<template>
    <div class="root">
        <b-navbar toggleable="lg" type="dark" variant="dark" class="p-3">
            <router-link to="/"><b-navbar-brand>
                    <p class="h2">welcome</p>
                </b-navbar-brand></router-link>

            <b-navbar-toggle target="nav-collapse" class="ml-auto"></b-navbar-toggle>

            <b-collapse id="nav-collapse" is-nav class="bg-transparent">


                <b-navbar-nav class="ml-auto"> 
                    <b-button text="Lang" type="button" pill right variant="light" class="mr-2 px-2 " @click="ToAdmin">
                        訂單管理
                    </b-button>
                    
                    <b-button text="Lang" type="button" pill right variant="light" class="mr-2 px-2" @click="ToShopPage">
                        商品
                    </b-button>

                    <b-button text="Lang" type="button" pill right variant="light" class="mr-2 px-2"  @click="ToCart">
                        <b-badge variant="light" class="mx-2 bg-light">{{ CartItem.length }}</b-badge> 購物車 
                    </b-button>

                    <b-button text="Lang" type="button" pill right variant="light" v-if="!isLogin" @click="showModal">
                        登入 
                    </b-button>
                    
                    
                    <b-dropdown text="用戶專區" right variant="light" v-if="isLogin" class="mr-2">
                        <b-dropdown-item @click="ToHome" >首頁</b-dropdown-item>
                        <b-dropdown-item class="pt-1 pb-1" @click="ToOrderHistory">個人資訊</b-dropdown-item>
                        <b-dropdown-item @click="RemoveIdentify">登出</b-dropdown-item>
                    </b-dropdown>
                
                </b-navbar-nav>
            </b-collapse>

            <div>
                <b-modal ref="my-modal" hide-footer title="Login" cols="12">
                    <div class="d-block text-center">
                        <b-form-input v-model="account" placeholder="Enter your account"></b-form-input>
                        <b-form-input v-model="password" class="mt-1" placeholder="Enter your password"></b-form-input>
                    </div>
                    <b-button class="mt-2" variant="outline-warning" block @click="userlogin">Login</b-button>
                    <b-button class="mt-3" variant="outline-danger" block @click="hideModal">Close</b-button>

                </b-modal>
            </div>
        </b-navbar>
    </div>
</template>
  
<script>
import { RouterLink } from 'vue-router'
import { mapState } from 'vuex'


export default {
    name: 'NewTopBar',
    components: {
        RouterLink
    },
    data() {
        return {
            account: 'webtest1',
            password: '123456',
            isLogin: false,
        }
    },

    methods: {
        async userlogin() {
            if (!this.account || !this.password) {
                alert('不得為空')
            } else {
                if (this.account == 'webtest1' && this.password == '123456'){
                this.username = ''
                this.password = ''
                this.isLogin = true
                localStorage.setItem("authTokenAccess", 'userLogin')
                this.hideModal()
                if (this.$route.path != '/') {
                    this.$router.push({
                        path: '/'
                    })
                }} else {
                    alert('帳號或密碼錯誤')
                }
                }
            }
        ,
        showModal() {
            this.$refs['my-modal'].show()
        },
        hideModal() {
            this.$refs['my-modal'].hide()
        },
        ToHome(){
            if (this.$route.path != '/') {
                this.$router.push({
                    path: '/'
                })
            }
        },
        ToCart() {
            if (this.$route.path != '/mycart') {
                this.$router.push("/mycart");
            }
        },
        ToOrderHistory(){
            if (this.$route.path != '/OrderHistory') {
                this.$router.push("/OrderHistory");
            }
        },
        ToShopPage(){
            if (this.$route.path != '/ShopPage') {
                this.$router.push("/ShopPage");
            }
        },
        ToAdmin(){
            if (this.$route.path != '/adminlogin') {
                this.$router.push("/adminlogin");
            }
        },
        RemoveIdentify() {
            (localStorage.removeItem("authTokenAccess"))
            this.isLogin = false
            this.ToHome()
        }
    },
    computed: {
        ...mapState(["CartItem"])
    },
    created(){
        if (localStorage.getItem("authTokenAccess")){
            this.isLogin = true
        }
    }
}



</script>
  
<style scoped>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Noto Sans TC', sans-serif;
    z-index: 999;
}
</style>