<template>
    <div class="checkpage">

        <div v-if="!itemcheck">
            <div class="itemlist">
                <b-container fluid class="text-center p-1" cols="12" lg="8">
                    <b-col class="p-3 md-auto">
                        <b-icon icon="card-checklist" aria-hidden="true"></b-icon>
                        <h2>訂單內容</h2>
                    </b-col>
                </b-container>
            </div>

            <div>
                <b-container col="12" lg="8" class="text-center mx-auto p-1" style="border-bottom: #bbbbbb 1px solid;">
                    <b-row>
                        <b-col>
                            <b-table striped :items="Order" :fields="fieldsitem" class="mx-auto"
                                >
                                <template v-slot:cell(TotalPrice)="{ item }">
                                    <span>
                                        {{ item.totalcount * item.price }}
                                    </span>
                                </template>
                            </b-table>
                        </b-col>
                    </b-row>
                </b-container>
                <b-container class="text-center mx-auto bg-transparent">
                    <b-row>
                        <b-col>
                            <div>
                                <h2 class="text-decoration-line-through">金額:{{ ItemTotalPrice }}</h2>
                            </div>
                            <div class="mx-auto ">
                                <h2>折扣後金額:{{ Math.floor(ItemTotalPrice * 0.85) }}</h2>
                            </div>
                            <b-button variant="gray" class="rounded-circle px-2 mt-2">
                                <b-icon icon="arrow-right" scale="2" @click="itemcheck = true"></b-icon>
                            </b-button>
                        </b-col>
                    </b-row>
                </b-container>
            </div>
        </div>

        <div v-if="itemcheck">
            <b-container col="12" lg="8" class="mx-auto p-1" >
            <b-form>
                <b-form-group class="mt-1" id="input-group-2" label="收件人姓名:" label-for="input-2">
                    <b-form-input id="input-2" v-model="name" required></b-form-input>
                </b-form-group>

                <b-form-group class="mt-1" id="input-group-1" label="收貨日期" label-for="example-datepicker">
                    <b-form-datepicker id="example-datepicker" v-model="date" class="mb-2" :date-disabled-fn="dateDisabled"
                        :min="min" :max="max" required></b-form-datepicker>
                </b-form-group>

                <b-form-group class="mt-1" id="input-group-2" label="地址:" label-for="input-2">
                    <b-form-input id="input-2" v-model="address" required></b-form-input>
                </b-form-group>

                <b-form-group class="mt-1" id="input-group-2" label="電話號碼:" label-for="input-2">
                    <b-form-input id="input-2" v-model="phone" required placeholder="09XXXXXXXX"></b-form-input>
                </b-form-group>

                
                <b-container class="p-1">
                    <label for="customCheck1" class="p-1">
                        <input type="radio" id="customCheck1" v-model="payment" value="1">
                        <span>超商繳費</span>
                    </label>
                
                
                    <label for="customCheck2" class="p-1">
                        <input type="radio" id="customCheck2" v-model="payment" value="2">
                        <span>貨到付款</span>
                    </label>
                </b-container>

                <b-button variant="outline-bold" class="my-3" @click="istrue">
                    <b-icon icon="credit-card" aria-hidden="true"></b-icon>送出訂單
                </b-button>
            </b-form>
        </b-container>

        </div>
    </div>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex';
import { reg_phoneType } from '@/utils/validate'

export default {
    name: 'CheckOut',
    data() {
        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        const minDate = new Date(today)
        const maxDate = new Date(today)
        maxDate.setMonth(maxDate.getMonth() + 1)
        maxDate.setDate(15)
        return {
            itemcheck: false,
            name: '',
            date: '',
            address: '',
            phone: '',
            payment: '',
            fieldsitem: [{
                    "key": "title",
                    "label": "名稱"
                },
                {
                    "key": "totalcount",
                    "label": "數量"
                },
                {
                    "key": "TotalPrice",
                    "label": "小計"
                }],
            min: minDate,
            max: maxDate
        }
    },
    computed: {
        ...mapState(['TotalPrice', 'CartItem']),
        ...mapState(["Order"]),
        ...mapGetters(["DisCount", "DiscountPrice", "ItemTotalPrice", "ItemCount"])

    },
    methods: {
        ...mapMutations(['CountItemTotalPrice', 'SEND_ORDER']), //methods有指定value為n 但這裡沒有指定param所以必須在func回傳

        istrue() {
            if (
                !this.name || !this.date || !this.address || !this.phone) {
                alert('資料不得為空')
            }
            else if (!this.payment) {
                alert('請選擇一種付款方式')
            }
            else if (!reg_phoneType(this.phone)) {
                alert('電話格式錯誤')
            }
            else {
                this.PURCHASE()
            }

        },

        async PURCHASE() {
            try {
                const today = new Date();
                const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
                const time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
                const dateTime = date + ' ' + time;

                if (this.$store.state.Order.length != 0) {
                    let total = await this.$store.state.Order.reduce(
                        (total, item) => {
                            return total + item.price * item.totalcount
                        }, 0
                    )

                    let order_send = {
                        "_id": Math.random(),
                        "orderContent": [],
                        "order_price": Math.floor(total * 0.85),
                        "receiver": this.name,
                        "phone_number": this.phone,
                        "payment": this.payment,
                        "date": this.date,
                        "address": this.address,
                        "createdAt": dateTime,
                        'order_status': 'O'
                    };

                    (async () => {
                        await this.$store.state.Order.forEach(item => {
                            order_send.orderContent.push({
                                "product": item.title,
                                "order_quantity": item.totalcount,
                                "order_item_price": item.price
                            })
                        }
                        )
                    })();



                    this.SEND_ORDER(order_send);

                    this.$router.push({
                        path: '/OrderHistory'
                    });

                    (() => {
                        this.$store.state.Order.forEach(item => {
                            let ItemEmpty = this.$store.state.CartItem.findIndex((x) => x.title === item.title)
                            this.$store.state.CartItem.splice(ItemEmpty, 1)
                        })
                        this.$store.state.Order.splice(0, this.$store.state.Order.length)
                    })()

                }
                else {
                    throw alert('購物車內無東西!')
                }

            } catch (err) {
                console.log(err)
            }

        },


        dateDisabled(ymd, date) {
            const weekday = date.getDay()
            const day = date.getDate()
            return weekday === 0 || weekday === 6 || day === 13
        }
    }
}




</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
    border: 0
}

.checkpage {
    height: 100vh;
    background-color: #FBF3DE;
}

.itemlist {
    border-bottom: #bbbbbb 1px solid;
}
</style>