<template>
    <div class="container">
        <b-container sm="12" md="10" lg="8" class="mx-auto">
        <h3>訂單管理</h3>
        <div>
            <label for="example-datepicker">到貨日期查詢</label>
            <b-form-datepicker id="example-datepicker" v-model="date" class="mb-2"></b-form-datepicker><button @click="filter(date)">查詢</button>
        </div>

        <h4>所有訂單</h4>
        <b-table striped  :fields="field" :items="order" class="mx-auto px-2">
            <template #cell(id)="{ item }">
                <span @click="detail(item._id)" class="pl-2 bg-transparent">
                    {{ item._id }}
                </span>
            </template>

            <template #cell(到貨日期)="{ item }">
                <span class="bg-transparent">
                    {{ item.date.substring(0,10) }}
                </span>
            </template>

            <template #cell(createdAt)="{ item }" >
                <span class="bg-transparent">
                    {{ item.createdAt.substring(0,10) }}
                </span>
            </template>

            <template #cell(orderstatus)="{ item }">
                <span class="bg-transparent">
                    {{ orderstatus[item.order_status]}}
                </span>
            </template>
        </b-table>
    </b-container>
    </div>

</template>

<script>
import { mapState } from 'vuex';

export default{
    name:"AdminPage",
    data(){
        return{
            order:'',
            date:'',
            orderstatus:{
                "O":"訂單已下訂",
                "R":"收到訂單",
                "D":"訂單處理中",
                "S":"出貨中",
                "F":"訂單完成",
            },
            field: [
                {
                    "key": "id",
                    "label": "訂單編號"
                }, 
                {
                    "key": "order_price",
                    "label": "金額"
                }, 
                {
                    "key": "date",
                    "label": "到貨日期"
                },
                {
                    "key": "createdAt",
                    "label": "訂單下訂日期"
                },
                {
                    "key": 'orderstatus',
                    "label": "訂單狀態"
                },                        
            ]
        }
    },

    computed:{
        ...mapState(["OrderDetail"])
    },
    created(){
        this.order = JSON.parse(JSON.stringify(this.OrderDetail))
    },
    methods:{
        async filter(date){
            let conform_order = this.OrderDetail.filter(item =>                
                item.date == date
            )
            this.order = conform_order
        }
        ,

        detail(id){
            this.$router.push({
                path:`/AdminOrderDetail/${id}`,
            })
        }
}
}


</script>

<style scoped>
 *{
    height: 100%;
}

label input{
    display: block;
}
input{
    border:1px solid rgba(0,0,0,1)
}
h4{
    margin: 20px 0px;
}


.pl-2:hover{
    color: #dddddd;
    cursor: pointer;
}
</style>