<template>
  <div class="webcart">
    <div v-if="CartItem.length == 0">
      <h4 class="mx-auto">購物車內無東西</h4>
    </div>

    <b-container sm="12" md="10" lg="8" cols="12" class="mx-auto">
      <b-card v-for="item in CartItem" :key="item.index" class="mb-3 p-1" cols="12">
        <b-row>
          <b-input-group-prepend is-text class="mr-2">
            <input type="checkbox" @click="selectitem(item)" v-model="item.isSelected" v-bind:item="item.isSelected">
          </b-input-group-prepend>
          <b-img center v-bind="mainProps" :src="item.imageURL" alt="Image"></b-img>
          <b-col class="p-2">
            <b-card-title>{{ item.title }}</b-card-title>
            <b-card-sub-title>${{ item.price.toString()}}</b-card-sub-title>
            <b-card-text class="mt-4">
              <b-btn @click="ITEMDECREMENT(item)"><b-icon icon="bookmark-dash-fill" aria-hidden="true"></b-icon></b-btn>
              <b-badge variant="light" class="mx-2 bg-light">{{ item.totalcount }}</b-badge>
              <b-btn @click="ITEMINCREMENT(item)"><b-icon icon="bookmark-plus-fill" aria-hidden="true"></b-icon></b-btn>
            </b-card-text>
          </b-col>
          <b-col>
            <b-button @click="DELETEITEM(item)" class="align-top float-right bg-transparent p-1" ><b-icon icon="cart-x" scale="1.2"
                class="mb-1" variant="danger"></b-icon></b-button>
          </b-col>
        </b-row>
      </b-card>

    </b-container>

    <div v-if="CartItem.length != 0" class="exist">
      <b-container class="text-center mx-auto" cols="12">
          <b-list-group>
            <b-list-group-item class="d-flex align-items-center bg-transparent">
              <span class="mr-auto my-2">
                <h2>選取的件數:{{ ItemCount }}</h2>
              </span>
            </b-list-group-item>
            <b-list-group-item class="d-flex align-items-center bg-transparent ">
              <span class="mr-auto my-2">
                <h2>總金額:{{ ItemTotalPrice }}</h2>
              </span>
            </b-list-group-item>
          </b-list-group>
          <router-link to="/CheckOut">
            <b-button variant="outline-gray" class="p-2" v-if="Order.length != 0">
              <b-icon icon="cart-check-fill" aria-hidden="true"></b-icon>
              購買
            </b-button>
          </router-link>
      </b-container>
    </div>

  </div>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex';

export default {
  name: "WebCart",
  data() {
    return {
      fields: ['check', 'title', 'price', 'quantity', 'totalprice', 'delete'],
      mainProps: {
        center: true,
        width: 150,
        height: 150,
        class: 'my-1 mx-1'
      }
    };
  },
  computed: {
    ...mapState(["CartItem", "Order"]),
    ...mapGetters(["DiscountPrice", "ItemTotalPrice", "ItemCount"]), //取得getters的functions
  },
  methods: {
    ...mapMutations(["ITEMDECREMENT", "selectitem", "ITEMINCREMENT", "DELETEITEM"]),
  },
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Noto Sans TC', sans-serif;
}

</style>