<template>
    <div class="shop-container">
        <b-breadcrumb class="p-2">
            <b-breadcrumb-item>
                <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
                <router-link to="/"><a>首頁</a></router-link>
            </b-breadcrumb-item>
            <b-breadcrumb-item active>商品頁面</b-breadcrumb-item>
        </b-breadcrumb>

        <b-container cols="12" lg="8" class="mx-auto">  
            <div class="navbar">
                <div class="activity">
                    <h3>排序:</h3>
                    <input type="radio" name="gender" value="2" v-model="SortType">名稱
                    <input type="radio" name="gender" value="3" v-model="SortType">金額
                    <!-- <h4>{{ SortType }}</h4> -->
                </div>
            </div>


            <div class="mx-auto">
                <b-card-group deck class="ml-4">
                    <b-card no-body style="max-width: 22rem; min-width: 20rem;" class="ml-1 mr-1 p-2"
                        :img-src="TestData.imageURL" img-alt="Image" img-top v-for="TestData in itemData"
                        :key="TestData._id">

                        <b-card-body>
                            <b-card-title @click="showdetail(TestData.id, TestData)" class="title">{{ TestData.title }}</b-card-title>
                            <b-card-sub-title class="mb-2 mt-1">${{ TestData.price }}</b-card-sub-title>
                            <b-card-text>
                                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Eos nisi, molestias ratione
                                veritatis
                                tempore facilis, labore minus culpa soluta mollitia itaque maiores unde quae eligendi
                                voluptas
                                quibusdam. Doloremque, asperiores earum!
                            </b-card-text>
                        </b-card-body>
                        <b-button size="lm" class="mb-2 p-1 mt-2" style="max-width: 10rem;" @click="ItemADDCART(TestData)">
                            <b-icon icon="cart-plus" aria-hidden="true"></b-icon> 加入購物車
                        </b-button>
                    </b-card>
                </b-card-group>
            </div>
            <b-alert class="fixed-top mx-4 mt-4" :show="dismissCountDown" variant="info"
                @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged" cols="12">
                加入購物車
            </b-alert>
        </b-container>

    </div>
</template>
  
<script>
import { mapMutations, mapActions } from 'vuex'

export default {
    name: 'ShopPage',
    data() {
        return {
            show: true,
            SortType: '',
            itemData: [
                {
                    '_id': '001',
                    'title': 'coriolanus',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare---coriolanus-book-cover.png?raw=true',
                    'price': 1605,
                    'intro': 'Coriolanus is a tragedy play written by William Shakespeare. It was likely written between 1605 and 1608 and was first performed in 1611. The play is set in ancient Rome and follows the story of Caius Marcius Coriolanus, a successful and valiant Roman general. Coriolanus struggles with his pride, loyalty, and the complex relationship between the common people and the ruling elite. The play delves into themes of power, politics, and the consequences of pride and ambition. Overall, Coriolanus is a compelling exploration of human nature and the complexities of society.'
                },
                {
                    '_id': '002',
                    'title': 'titus-andronicus',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare---titus-andronicus-book-cover.png?raw=true',
                    'price': 1594,
                    'intro':"Titus Andronicus is a tragedy play written by William Shakespeare, believed to be one of his earliest works. The play is set in ancient Rome and tells the dramatic and violent story of the Roman general, Titus Andronicus, and his descent into madness and revenge. It is known for its graphic and brutal scenes, as well as its exploration of themes such as honor, power, and the corrupting nature of vengeance. While not as well-known as some of Shakespeare's other works, Titus Andronicus is a powerful and thought-provoking play that continues to captivate audiences with its dark and tragic tale."
                },
                {
                    '_id': '003',
                    'title': 'cymbeline',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare--cymbeline-book-cover.png?raw=true',
                    'price':1872 ,
                    'intro':"Cymbeline is a play by William Shakespeare that was likely written in 1610 or 1611 and was first performed in 1611. It is classified as a tragicomedy, meaning it combines elements of both tragedy and comedy. Set in ancient Britain, the play follows the story of Cymbeline, the king of Britain, and his daughter, Imogen, as they navigate through personal trials and political conflicts.Cymbeline is known for its complex plot, which involves themes of deception, betrayal, and mistaken identity. The play explores topics such as loyalty, love, and forgiveness, and also delves into the power struggles between different factions.One of the notable aspects of Cymbeline is its diverse range of characters, each with their own intricate development. Imogen, the protagonist, is a strong and virtuous young woman who defies societal expectations and is determined to fight for her own happiness. Other memorable characters include the manipulative Iachimo, the loyal Posthumus, and the estranged stepbrothers Guiderius and Arviragus.Throughout the play, Shakespeare weaves in elements of romance, comedy, tragedy, and even elements reminiscent of fairy tales. Cymbeline is often praised for its poetic language, intricate wordplay, and intriguing blend of various theatrical genres. Although it is not as well-known as some of Shakespeare's other works, Cymbeline offers a unique and compelling theatrical experience for audiences."
                },
                {
                    '_id': '004',
                    'title': 'timon-of-athens',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare--timon-of-athens-book-cover.png?raw=true',
                    'price':1890 ,
                    'intro':"Timon of Athens is a tragic play written by William Shakespeare. The play explores themes of wealth, friendship, and betrayal. It tells the story of Timon, a wealthy Athenian nobleman whose extravagant lifestyle leads to his downfall. Timon is surrounded by supposed friends who only seek his wealth and favors, but when his fortune runs dry, they abandon him. The play offers a biting critique of greed and hypocrisy. Although it is not as well-known as some of Shakespeare's other plays, Timon of Athens delves into complex issues and showcases the Bard's unique insight into human nature."
                },
                {
                    '_id': '005',
                    'title': 'hamlet',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-hamlet-book-cover.png?raw=true',
                    'price': 1604,
                    'intro':"Hamlet, often regarded as one of William Shakespeare's most famous plays, is a tragic story set in Denmark. The play revolves around the young prince Hamlet, who seeks revenge for the murder of his father, King Hamlet, by his uncle, Claudius. Throughout the play, Hamlet questions his existence, morality, and the nature of revenge, making it a thought-provoking and complex work. With its themes of betrayal, insanity, and the consequences of one's actions, Hamlet is a universally recognized and celebrated tragedy that continues to captivate audiences to this day."
                },
                {
                    '_id': '006',
                    'title': 'julius-caesar',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-julius-caesar-book-cover.png?raw=true',
                    'price':1623 ,
                    'intro':"Julius Caesar was a Roman military general and statesman who played a critical role in the transformation of the Roman Republic into the Roman Empire. He was born in 100 BC into a patrician family and quickly rose through the ranks in Roman politics and military. Caesar is famous for his military campaigns, particularly the Gallic Wars, where he gained significant conquests in Gaul (modern-day France) and expanded the Roman empire.Beyond his military prowess, Caesar was a skilled orator and politician. He formed alliances with influential figures, such as Pompey and Crassus, leading to the formation of the First Triumvirate, a political alliance that granted him immense power. However, his growing influence and popularity among the Roman people became a threat to the traditional republican system of government.In 49 BC, Caesar crossed the Rubicon River with his army, thus igniting a civil war against the Roman Senate and his political opponents. This act marked a point of no return and led to Caesar's victorious return to Rome, where he was appointed as a dictator perpetuo, effectively creating a dictatorship.Under Caesar's rule, he implemented several reforms like the Julian calendar, land reforms, and strengthening the power of the central government. He also initiated various construction projects and public works to improve the infrastructure of Rome. However, his authoritarian rule and disregard for the traditional republican institutions alarmed some senators, leading to a conspiracy against him.On the Ides of March (March 15) in 44 BC, Julius Caesar was assassinated by a group of senators led by Brutus and Cassius. His death sparked another round of power struggles in Rome, ultimately leading to the end of the Roman Republic and the rise of the Roman Empire under Augustus Caesar, Julius Caesar's adopted heir. Despite his controversial legacy, Julius Caesar remains one of the most influential figures in Roman history."
                },
                {
                    '_id': '007',
                    'title': 'king-lear',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-king-lear-book-cover.png?raw=true',
                    'price': 1864,
                    'intro':"King Lear is a tragedy written by William Shakespeare. It tells the story of King Lear, who in his old age, decides to divide his kingdom among his three daughters, based on how much they love him. However, he is deceived by his two elder daughters and ends up homeless and destitute. Along the way, many characters meet a tragic end, and the play explores themes of power, loyalty, family, and betrayal. King Lear is considered one of Shakespeare's greatest works and is still performed and studied extensively today."
                },
                {
                    '_id': '008',
                    'title': 'macbeth',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-macbeth-book-cover.png?raw=true',
                    'price':1884 ,
                    'intro':"Macbeth is a tragedy play written by William Shakespeare, which was first performed in 1606. The story is set in Scotland and follows a Scottish nobleman named Macbeth, who receives a prophecy from three witches that he will become the king of Scotland. Driven by ambition and greed, Macbeth is consumed by his desire to achieve the throne, leading him to commit a series of heinous crimes in order to maintain his newfound power. The play explores themes of fate, ambition, guilt, and the corrupting nature of power, making it one of Shakespeare's most well-known and frequently staged works."
                },
                {
                    '_id': '009',
                    'title': 'othello',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-othello-book-cover.png?raw=true',
                    'price': 1884,
                    'intro':"Othello is a tragedy by William Shakespeare about the Venetian general, Othello, who falls in love with and marries Desdemona, the daughter of a wealthy senator. However, a jealous subordinate, Iago, convinces Othello that Desdemona is unfaithful, leading to a tragic chain of events that culminates in the deaths of several characters, including Othello and Desdemona. The play explores themes of jealousy, betrayal, racism, and the struggle between light and darkness within the human soul. It is considered one of Shakespeare's greatest works and has been adapted numerous times for stage, film, and television."
                },
                {
                    '_id': '010',
                    'title': 'romeo-and-juliet',
                    'imageURL': 'https://github.com/small876/item_img/blob/main/william-shakespeare-romeo-and-juliet-book-cover.png?raw=true',
                    'price': 1870,
                    'intro':"Romeo and Juliet is a tragic play written by William Shakespeare in the late 16th century. It is one of his most famous and enduring works, exploring themes of love, fate, and feuding families. The play tells the story of two young lovers, Romeo and Juliet, who belong to rival households in the city of Verona. Despite the ongoing feud between their families, Romeo and Juliet fall deeply in love and secretly marry. However, their love is put to the test as their families' conflict escalates, leading to a series of tragic events that ultimately results in their untimely deaths. Romeo and Juliet is known for its poetic language, memorable characters, and timeless portrayal of young love. It remains one of the most frequently performed and studied works of literature to this day."
                },
            ],
            dismissCountDown: 0
        }
    },

    watch: {
        SortType: {
            handler(newValue) {
                if (newValue == 2) {
                    this.itemData.sort(function (a, b) {
                        var titleA = a.title.toUpperCase(); // ignore upper and lowercase
                        var titleB = b.title.toUpperCase(); // ignore upper and lowercase
                        if (titleA < titleB) {
                            return -1;
                        }
                        if (titleA > titleB) {
                            return 1;
                        }

                        // titles must be equal
                        return 0;
                    })
                }
                else if (newValue == 3) {
                    this.itemData.sort(function (a, b) {
                        if (a.price < b.price) {
                            return 1;
                        }
                        if (a.price > b.price) {
                            return -1;
                        }
                        return 0;
                    })
                }
            }	 //handler會在發生改變時呼叫
        }
    },

    methods: {
        ...mapMutations(['ADDCART','SHOW_ITEM_DETAIL']),
        ...mapActions(['ItemIncrement']),

        ItemADDCART(item) {
            this.ItemIncrement(item)
            this.dismissCountDown = 1
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },

        showdetail(id, item) {
            this.SHOW_ITEM_DETAIL(item)
            this.$router.push({
                path: `/ItemDetail/${id}`,
            })
        },
    }
}



</script>
  
<style scoped>
.shop-container {
    background-color: #FBF3DE
}

/* #region############################################## navbar  ##################################################### */
.navbar h3 {
    display: inline;
}


.navbar input {
    position: relative;
    ;
    margin: 0px 10px;

}

.title:hover {
    color: #dddddd;
    cursor: pointer;
}
/* #endregion */
</style>