<template>
    <div class="container">
        <h3>登入</h3>
        <label for='account'>帳號</label><br>
        <input id='account' type="text" placeholder="enter account" v-model="account"><br>
        <label for='password'>密碼</label><br>
        <input id='password' type="password" placeholder="enter password" v-model="password"><br>
        <button @click="login">登入</button>
    </div>

</template>

<script>
export default{
    name:"AdminLogin",
    data(){
        return{
            account:'T000001',
            password:'123456'
        }
    },
     methods:{
        async login(){
            if (!this.account || !this.password) {
                alert('不得為空')
            } else {
                if (this.account == 'T000001' && this.password == '123456'){
                    this.$router.push({
                        path:'/admin'
                    })
                }else {
                    alert('帳號或密碼錯誤')
                }
            }
            }
     }
    }

</script>

<style scoped>
label input{
    display: block;
}
input{
    border:1px solid rgba(0,0,0,1)
}

</style>