<template>
    <div class="itemdetailcontainer">
        <b-breadcrumb class="p-2">
            <b-breadcrumb-item>
                <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
                <router-link to="/"><a>首頁</a></router-link>
            </b-breadcrumb-item>
            <b-breadcrumb-item>
                <router-link to="/ShopPage"><a>商品頁面</a></router-link>
                
            </b-breadcrumb-item>
            <b-breadcrumb-item active>商品信息</b-breadcrumb-item>
        </b-breadcrumb>

        <b-container>
            <b-row>
                <b-col cols="12" class="mx-auto mt-1">
                    <b-card class="mb-3 mx-auto">
                        <b-card-img :src="itemdata.imageURL" loading="lazy" alt="Image" class="rounded-0"></b-card-img>
                        <b-card-title><h3 class="detail">{{ itemdata.title }}</h3></b-card-title>
                        <b-card-sub-title class="mb-2 mt-1">${{ itemdata.price }}</b-card-sub-title>
                        <b-card-text>
                            {{ itemdata.intro }}
                        </b-card-text>
                        <b-button size="lm" class="mb-2 p-1 mt-2" style="max-width: 10rem;" @click="ItemADDCART(itemdata)">
                            <b-icon icon="cart-plus" aria-hidden="true"></b-icon> 加入購物車
                        </b-button>
                    </b-card>
                </b-col>
            </b-row>
        </b-container>
        <b-alert class="fixed-top ml-4 mt-4" cols="12" :show="dismissCountDown" variant="info"
            @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged">
            加入購物車
        </b-alert>
    </div>
    
</template>
  
<script>

import { mapMutations, mapActions, mapState } from 'vuex'

export default {
    name: 'ItemDetail',
    data() {
        return {
            dismissCountDown: 0,
            itemdata:''
        }
    },

    computed:{
        ...mapState(['ItemDetail'])
    },

    methods: {
        ...mapMutations({ ADDCART: 'ADDCART' }),
        ...mapActions({ ItemIncrement: 'ItemIncrement' }),
        ItemADDCART(item) {
            this.ItemIncrement(item)
            this.dismissCountDown = 1
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
    },

    created(){
        this.itemdata = this.ItemDetail
    }
}


</script>
  
<style>
.detail{
    font-size: 5rem;
    font-family: 'Great Vibes','cursive';
}
</style>